const CustomHR = (props) => {

    const { begunModule, allModules, companyData, companyUsersArray, goto, components, authenticatedUser, user, companyProcesses, template } = props;
    const { title, selectedComponent, setSelectedComponent, step } = props;
    const [loading, setLoading] = useState(false);

    return <div className={clsg.accordionContainer}>
        <div style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: isMobile ? '18px' : '20px', fontWeight: 'bold', marginTop: isMobile ? '' : '50px' }}>
            {title}
        </div>
    </div>;
}

const mapStateToProps = store => {
    const {
        authenticatedUser: { position },
        authenticatedUser,
    } = store.authReducer;
    const { user } = store.sessionReducer;

    const { begunModule, allModules, companyData, companyUsersArray, companyPositionsArray, components, companyProcesses } = store.flowReducer;
    return {
        begunModule, allModules, companyData, companyUsersArray, companyPositionsArray, components, authenticatedUser, user, companyProcesses
    };
};


export default connect(mapStateToProps)(CustomHR);

export const CustomHRTimesheetStep = (props) => {
    const { selectedComponent, begunModule, goto } = props;
    const { companyData, companyUsersArray, components } = useSelector(store => store.flowReducer);
    const { authenticatedUser } = useSelector(store => store.authReducer);

    const [loading, setLoading] = useState(false);
    const [items, setItems] = useState(selectedComponent?.component_items?.length > 0 ? selectedComponent?.component_items : [{ item_code: '', value: '', alt_value: '', id: 0 }])

    const [processTaskContext, setTaskProcessContext] = useProcessTaskContext();

    const addItemButton = () => (
        <Button
            light
            style={{ display: '' }}
            clicked={() => {
                setItems([...items, { item_code: '', value: '', alt_value: '', id: items.length }])
            }}
            icon={<img src={add} />}
        />
    );

    const DateInput = props => {

        return (

            <div style={{ width: '100%', height: 'auto', border: '0' }}>

                <DatePicker
                    selected={props?.value ? new Date(props?.value) : new Date()}
                    showTimeSelect
                    timeFormat="HH:mm"
                    timeIntervals={5}
                    customInput={
                        <div
                            style={{
                                cursor: 'pointer',
                                display: 'flex',
                                justifyContent: 'center',
                                width: '100%',
                                alignItems: 'stretch',
                                padding: '5px',
                                border: '0px solid var(--app-color-gray-lighter)',
                            }}
                        >
                            <FontAwesomeIcon icon={faCalendarDays} style={{ margin: '0 10px', width: '20px', height: '20px' }} />
                            <input
                                value={props.value ? moment(props.value).format('DD MMMM YYYY HH:mm') : ''}
                                //disabled
                                className={cls.inputValue}
                                type="text"
                                style={{
                                    flex: 1,
                                    marginTop: 'auto',
                                    marginBottom: 'auto',
                                    outline: 'none',
                                    fontFamily: 'var(--app-text-main-font)',
                                    fontSize: '16px',
                                    lineHeight: '20px',
                                    color: '#4A4A4A',
                                    textAlign: 'left',
                                    maxWidth: '98%',
                                    border: '0px'

                                }}
                                onChange={_e => { }}
                            />

                        </div>
                    }
                    className={cls.singleInputContainer}
                    onChange={date => {
                        props.handleDate(date);
                    }}
                />
            </div>
        )
    }

    return <div className={clsg.accordionContainer}>
        <div style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: isMobile ? '18px' : '20px', fontWeight: 'bold', marginTop: isMobile ? '' : '50px', padding: '20px' }}>
            {selectedComponent?.id ? <ModuleLists
                className={cls.itemsTable}
                columns={['Time In', 'Time Out', 'Details', 'Duration', 'Options']}
                list={items?.map(itm => {
                    return {
                        ['Time In']: <DateInput value={itm.value} style={{ width: '100%', border: '1px solid black' }}
                            handleDate={date => {
                                itm.value = date;

                                setItems([...items.filter(it => it.id !== itm.id), itm]);
                            }}
                        />,
                        ['Time Out']: <DateInput value={itm.alt_value} style={{ width: '100%', border: '1px solid black' }}
                            handleDate={date => {
                                itm.alt_value = date;
                                setItems([...items.filter(it => it.id !== itm.id), itm]);
                            }}
                        />,
                        ['Details']: <input defaultValue={itm.item_code}
                            className={cls.inputValue}
                            style={{
                                fontWeight: 'normal',

                            }}
                            onChange={e => {
                                itm.item_code = e.target.value
                            }}
                            onBlur={() => {
                                setTaskProcessContext({
                                    action: processTaskAction.setModuleItems,
                                    value: items,
                                });
                            }}
                        />,
                        ['Duration']: <input
                            defaultValue={!(itm.value || itm.alt_value) ? '0' : moment(new Date(itm.alt_value), 'DD/MM/YYYY').diff(moment(new Date(itm.value), 'DD/MM/YYYY'), 'hours')}
                            className={cls.inputValue}
                            style={{
                                fontWeight: 'normal',

                            }}
                        />,
                        ['Options']: <FontAwesomeIcon icon={faTrash} onClick={() => {
                            setTaskProcessContext({
                                action: processTaskAction.setModuleItems,
                                value: items.filter(it => it.id !== itm.id),
                            });
                            setItems(items.filter(it => it.id !== itm.id));
                        }} />,
                        id: itm.id
                    };
                })}
                footer={[<br key={0} />, <br key={1} />, <input value={'Total'} key={2} className={cls.inputValue} />, <input key={3} className={cls.inputValue}
                    defaultValue={
                        `${R.reduce((a, b) => +a + (!(b.value || b.alt_value) ? 0 : moment(new Date(b.alt_value), 'DD/MM/YYYY').diff(moment(new Date(b.value), 'DD/MM/YYYY'), 'hours')), 0, items ?? [])}`} />, <br key={4} />]}
            /> : props.children}
            <div style={{ display: 'flex', justifyContent: 'left' }}>
                {addItemButton()}
            </div>
        </div>



    </div>;


}
